import React from 'react';

function Loading() {
  return (
    <div className="fixed inset-0 flex flex-col items-center justify-center z-50" style={{ backgroundColor: 'black' }}>
      {/* Three dots loading animation */}
      <div className="flex space-x-2 mb-4">
        <div className="w-4 h-4 bg-white rounded-full animate-dot1" />
        <div className="w-4 h-4 bg-white rounded-full animate-dot2" />
        <div className="w-4 h-4 bg-white rounded-full animate-dot3" />
      </div>

      {/* Loading text */}
      <h2 className="text-white text-lg font-bold">Loading...</h2>

      {/* Moving horizontal line that changes background color */}
      <div className="absolute bottom-0 left-0 w-full h-1 bg-gray-600">
        <div className="h-full bg-black animate-line" />
      </div>

      <style jsx>{`
        @keyframes line-animation {
          0% {
            transform: translateY(100%);
            background-color: gray;
          }
          100% {
            transform: translateY(0);
            background-color: black;
          }
        }
        .animate-line {
          animation: line-animation 1s forwards;
        }
        @keyframes dot-animation {
          0%, 20% {
            transform: scale(1);
          }
          50% {
            transform: scale(1.5);
          }
          100% {
            transform: scale(1);
          }
        }
        .animate-dot1 {
          animation: dot-animation 0.6s ease-in-out infinite;
          animation-delay: 0s;
        }
        .animate-dot2 {
          animation: dot-animation 0.6s ease-in-out infinite;
          animation-delay: 0.2s;
        }
        .animate-dot3 {
          animation: dot-animation 0.6s ease-in-out infinite;
          animation-delay: 0.4s;
        }
      `}</style>
    </div>
  );
}

export default Loading; 
