import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

function Home() {
  const [scrollPosition, setScrollPosition] = useState(0);
  const [isZoomComplete, setIsZoomComplete] = useState(false);
  const [sectionScroll, setSectionScroll] = useState(0);
  const [visibleSections, setVisibleSections] = useState([false, false, false]);

  useEffect(() => {
    const handleScroll = (e) => {
      const delta = e.deltaY;
      
      // If we're at the top of the page and scrolling up, prevent default
      if (window.scrollY === 0 && delta < 0) {
        e.preventDefault();
        setScrollPosition(prev => {
          const newPosition = Math.max(0, prev + delta);
          setIsZoomComplete(false);
          return newPosition;
        });
      }
      // If we're in the zoom effect phase
      else if (!isZoomComplete) {
        e.preventDefault();
        setScrollPosition(prev => {
          const newPosition = prev + delta;
          if (newPosition >= 1000) {
            setIsZoomComplete(true);
            return 1000;
          }
          return Math.max(0, Math.min(newPosition, 1000));
        });
      }
    };

    const handleSectionScroll = () => {
      setSectionScroll(window.scrollY);
    };

    window.addEventListener('wheel', handleScroll, { passive: false });
    window.addEventListener('scroll', handleSectionScroll);

    const options = {
      root: null,
      rootMargin: '0px',
      threshold: 0.1
    };

    const observer = new IntersectionObserver((entries) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          const index = parseInt(entry.target.dataset.index);
          setVisibleSections(prev => {
            const newVisibleSections = [...prev];
            newVisibleSections[index] = true;
            return newVisibleSections;
          });
          observer.unobserve(entry.target);
        }
      });
    }, options);

    const sections = document.querySelectorAll('.fade-section');
    sections.forEach((section) => {
      observer.observe(section);
    });

    return () => {
      window.removeEventListener('wheel', handleScroll);
      window.removeEventListener('scroll', handleSectionScroll);
      sections.forEach((section) => {
        observer.unobserve(section);
      });
    };
  }, [isZoomComplete]);

  const getSectionStyle = (sectionIndex) => {
    const sectionHeight = window.innerHeight;
    const startOffset = sectionIndex * sectionHeight;
    const endOffset = (sectionIndex + 1) * sectionHeight;
    const progress = Math.max(0, Math.min(1, (sectionScroll - startOffset) / (sectionHeight * 0.5)));
    
    return {
      transform: `translateY(${progress * 50}px)`,
      opacity: 1 - progress,
      transition: 'all 0.5s ease-out'
    };
  };

  return (
    <div className={`relative ${isZoomComplete ? '' : 'h-screen overflow-hidden'}`}>
      {/* Hero Section with dramatic zoom effect */}
      <div className="relative h-screen overflow-hidden">
        <div 
          className="absolute inset-0 bg-cover bg-center"
          style={{ 
            backgroundImage: 'url("/images/f1.jpeg")',
            transform: 'scale(1)',
            transition: 'transform 0.1s ease-out'
          }}
        />
        <div 
          className="relative h-screen flex items-center justify-center bg-black"
          style={{
            opacity: Math.max(0, 1 - scrollPosition * 0.004),
            transition: 'opacity 0.3s ease-out'
          }}
        >
          <div 
            className="text-center"
            style={{
              transform: `scale(${1 + scrollPosition * 0.008}) translateY(${scrollPosition * -0.5}px)`,
              transition: 'all 0.3s ease-out'
            }}
          >
            <h1 
              className="text-[20rem] font-black tracking-tight text-transparent bg-clip-text leading-none"
              style={{
                backgroundImage: 'url("/images/f1.jpeg")',
                backgroundSize: 'cover',
                backgroundPosition: 'center',
                backgroundClip: 'text',
                WebkitBackgroundClip: 'text',
                color: 'transparent',
                textShadow: '0 0 30px rgba(255,255,255,0.5)',
                letterSpacing: '-0.05em',
                transform: 'perspective(1000px) rotateX(0deg)',
                transformOrigin: 'center center'
              }}
            >
              ANAKTO
            </h1>
          </div>
        </div>

        {/* Welcome Heading */}
        <div 
          className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 text-center z-50"
          style={{
            opacity: isZoomComplete ? 1 : 0,
            transition: 'opacity 0.5s ease-out',
            transform: `translate(-50%, ${isZoomComplete ? '-50%' : '-30%'})`,
            transition: 'all 0.5s ease-out'
          }}
        >
          <h2 className="text-4xl md:text-6xl font-bold text-white mb-4 transform hover:scale-105 transition duration-300">
            Welcome to ANAKTO
          </h2>
          <p className="text-xl md:text-2xl text-gray-300 transform hover:scale-105 transition duration-300">
            Your Gateway to Secure Crypto Management
          </p>
        </div>
      </div>

      {/* Features Section with f2 background */}
      <div className={`relative h-screen fade-section ${visibleSections[0] ? 'fade-in' : ''}`} data-index="0" style={getSectionStyle(1)}>
        <div 
          className="absolute inset-0 bg-cover bg-center"
          style={{ backgroundImage: 'url("/images/f2.jpeg")' }}
        />
        <div className="relative h-screen flex items-center justify-center bg-black bg-opacity-50">
          <div className="mx-auto max-w-7xl px-6 lg:px-8 py-10">
            <div className="mx-auto max-w-2xl lg:text-center">
              <h2 className="text-base font-semibold leading-7 text-green-400 hover:text-green-600 transition duration-300">Features</h2>
              <p className="mt-2 text-3xl font-bold tracking-tight text-white sm:text-4xl fade-in-text hover:scale-105 transition duration-300">
                Everything you need to manage your crypto
              </p>
            </div>
            <div className="mx-auto mt-8 max-w-2xl sm:mt-12 lg:mt-16 lg:max-w-none">
              <dl className="grid max-w-xl grid-cols-1 gap-x-8 gap-y-8 lg:max-w-none lg:grid-cols-3">
                {features.map((feature, index) => (
                  <div 
                    key={feature.name} 
                    className="flex flex-col transform transition-all duration-500 hover:scale-105 hover:shadow-lg rounded-lg p-6 bg-white bg-opacity-90 backdrop-blur-sm hover:shadow-xl hover:rotate-2 hover:translate-y-1 hover:transition-all"
                    style={{ 
                      animationDelay: `${index * 200}ms`,
                      animation: 'slideIn 0.5s ease-out forwards',
                      opacity: 0,
                      transform: 'translateY(20px)'
                    }}
                  >
                    <dt className="flex items-center gap-x-3 text-base font-semibold leading-7 text-gray-900">
                      {feature.name}
                    </dt>
                    <dd className="mt-4 flex flex-auto flex-col text-base leading-7 text-gray-600">
                      <p className="flex-auto">{feature.description}</p>
                    </dd>
                  </div>
                ))}
              </dl>
            </div>
          </div>
        </div>
      </div>

      {/* Call to Action Section with f3 background */}
      <div className={`relative h-screen fade-section ${visibleSections[1] ? 'fade-in' : ''}`} data-index="1" style={getSectionStyle(2)}>
        <div 
          className="absolute inset-0 bg-cover bg-center"
          style={{ backgroundImage: 'url("/images/f3.jpeg")' }}
        />
        <div className="relative h-screen flex items-center justify-center bg-black bg-opacity-50">
          <div className="text-center">
            <h2 className="text-3xl font-bold text-white mb-8 hover:scale-105 transition duration-300">Ready to Get Started?</h2>
            <Link
              to="/wallet"
              className="inline-block bg-white text-green-600 px-8 py-3 rounded-md font-semibold shadow-lg hover:bg-gray-100 transform transition-all duration-300 hover:scale-105"
            >
              Create Your Wallet
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}

const features = [
  {
    name: 'Secure Storage',
    description: 'Your digital assets are protected with state-of-the-art security measures.',
  },
  {
    name: 'Easy Transactions',
    description: 'Send and receive cryptocurrency with just a few clicks.',
  },
  {
    name: 'Real-time Updates',
    description: 'Stay updated with live cryptocurrency prices and portfolio tracking.',
  },
];

export default Home;