import React, { useState } from 'react';

function About() {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');
  const [submitStatus, setSubmitStatus] = useState({ show: false, success: false, message: '' });
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);
    setSubmitStatus({ show: false, success: false, message: '' });

    try {
      const response = await fetch('http://localhost:5000/api/contact', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ email, message }),
      });

      let data;
      try {
        data = await response.json();
      } catch (parseError) {
        throw new Error('Unable to connect to server. Please check your internet connection.');
      }

      if (response.ok) {
        setSubmitStatus({
          show: true,
          success: true,
          message: 'Message sent successfully! We will get back to you soon.'
        });
        setEmail('');
        setMessage('');
        setTimeout(() => {
          setIsModalOpen(false);
          setSubmitStatus({ show: false, success: false, message: '' });
        }, 2000);
      } else {
        throw new Error(data.message || data.error || 'Server error. Please try again.');
      }
    } catch (error) {
      setSubmitStatus({
        show: true,
        success: false,
        message: error.message || 'Network error. Please check your connection and try again.'
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="min-h-screen bg-transparent relative overflow-hidden">
      {/* Background video */}
      <div className="fixed top-0 left-0 w-full h-full z-0">
        <video
          autoPlay
          loop
          muted
          playsInline
          className="absolute top-0 left-0 min-w-full min-h-full object-cover opacity-30"
        >
          <source src="/videos/background.mp4" type="video/mp4" />
          Your browser does not support the video tag.
        </video>
      </div>

      <div className="relative z-10 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8 ">
        <h1 className="text-4xl font-bold text-white mb-8 text-center">About Us</h1>
        
        <div className="bg-white/80 backdrop-blur-sm shadow rounded-lg p-6 transform transition-all duration-500 hover:shadow-lg animate-fadeIn">
          <div className="space-y-8">
            <div className="transform transition-all duration-500 group">
              <h2 className="text-2xl font-bold text-gray-900 opacity-0 animate-slideInFromLeft hover:text-green-600 transition-colors duration-300 animate-glowPulse">
                About Anakto Wallet
              </h2>
              <p className="mt-4 text-gray-600 opacity-0 transform translate-x-[-50px] animate-[slideInFromLeft_0.8s_ease-out_0.2s_forwards] group-hover:translate-x-2 transition-transform duration-500">
                Anakto Wallet is a modern, secure, and user-friendly cryptocurrency wallet built with
                the latest web technologies. Our mission is to make cryptocurrency management
                accessible to everyone while maintaining the highest security standards.
              </p>
            </div>

            <div className="transform transition-all duration-500 group">
              <h3 className="text-xl font-semibold text-gray-900 opacity-0 animate-slideInFromLeft hover:text-green-600 transition-colors duration-300 animate-glowPulse" style={{ animationDelay: '0.3s' }}>
                Our Vision
              </h3>
              <p className="mt-4 text-gray-600 opacity-0 transform translate-x-[-50px] animate-[slideInFromLeft_0.8s_ease-out_0.5s_forwards] group-hover:translate-x-2 transition-transform duration-500">
                We believe in a future where digital assets are seamlessly integrated into everyday
                life. Our wallet is designed to bridge the gap between traditional finance and the
                cryptocurrency world, making it easy for both newcomers and experienced users to
                manage their digital assets.
              </p>
            </div>

            <div className="transform transition-all duration-500 group">
              <h3 className="text-xl font-semibold text-gray-900 opacity-0 animate-slideInFromLeft hover:text-green-600 transition-colors duration-300 animate-glowPulse" style={{ animationDelay: '0.6s' }}>
                Security First
              </h3>
              <p className="mt-4 text-gray-600 opacity-0 transform translate-x-[-50px] animate-[slideInFromLeft_0.8s_ease-out_0.8s_forwards] group-hover:translate-x-2 transition-transform duration-500">
                Security is our top priority. We implement industry-leading security measures to
                protect your assets, including:
              </p>
              <ul className="mt-4 list-disc list-inside space-y-2 text-gray-600">
                {[
                  'End-to-end encryption',
                  'Secure key management',
                  'Multi-factor authentication',
                  'Regular security audits'
                ].map((item, index) => (
                  <li 
                    key={item}
                    className="opacity-0 transform translate-x-[-50px] transition-all duration-300 hover:translate-x-2 hover:text-indigo-600"
                    style={{
                      animation: 'slideInFromLeft 0.8s ease-out forwards',
                      animationDelay: `${1.1 + (index * 0.2)}s`
                    }}
                  >
                    {item}
                  </li>
                ))}
              </ul>
            </div>

            <div className="transform transition-all duration-500 group">
              <h3 className="text-xl font-semibold text-gray-900 opacity-0 animate-slideInFromLeft hover:text-green-600 transition-colors duration-300 animate-glowPulse" style={{ animationDelay: '2s' }}>
                Technology Stack
              </h3>
              <p className="mt-4 text-gray-600 opacity-0 transform translate-x-[-50px] animate-[slideInFromLeft_0.8s_ease-out_2.2s_forwards] group-hover:translate-x-2 transition-transform duration-500">
                Built with modern technologies for optimal performance and security:
              </p>
              <ul className="mt-4 list-disc list-inside space-y-2 text-gray-600">
                {[
                  'React for a responsive and dynamic user interface',
                  'Web3.js for blockchain integration',
                  'TailwindCSS for modern styling',
                  'Industry-standard security protocols'
                ].map((item, index) => (
                  <li 
                    key={item}
                    className="opacity-0 transform translate-x-[-50px] transition-all duration-300 hover:translate-x-2 hover:text-indigo-600"
                    style={{
                      animation: 'slideInFromLeft 0.8s ease-out forwards',
                      animationDelay: `${2.5 + (index * 0.2)}s`
                    }}
                  >
                    {item}
                  </li>
                ))}
              </ul>
            </div>

            {/* Move contact button here, inside the white box */}
            <div className="flex justify-center opacity-1 transform translate-y-[20px] pt-4" style={{ animation: 'slideUp 0.8s ease-out 3.3s forwards' }}>
              <button
                onClick={() => setIsModalOpen(true)}
                className="inline-flex items-center px-6 py-3 border border-transparent text-base font-medium rounded-md shadow-sm text-white bg-green-600 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 transform transition-all duration-300 ease-in-out hover:scale-105 hover:shadow-lg active:scale-95"
              >
                Contact Us
              </button>
            </div>
          </div>
        </div>

        {/* Modal */}
        {isModalOpen && (
          <div className="fixed inset-0 z-[60] overflow-y-auto animate-fadeIn mt-16"> {/* Updated z-index and added mt-16 */}
            <div className="flex items-center justify-center min-h-[calc(100vh-4rem)] pt-4 px-4 pb-20 text-center sm:block sm:p-0"> {/* Updated min-h calculation */}
              {/* Background overlay */}
              <div 
                className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity"
                onClick={() => setIsModalOpen(false)}
              ></div>

              {/* Modal panel */}
              <div className="inline-block align-bottom bg-white rounded-lg px-4 pt-5 pb-4 text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg sm:w-full sm:p-6 animate-slideIn">
                <div className="absolute top-0 right-0 pt-4 pr-4">
                  <button
                    onClick={() => setIsModalOpen(false)}
                    className="bg-white rounded-md text-gray-400 hover:text-gray-500 focus:outline-none transform transition-all duration-200 hover:rotate-90"
                  >
                    <span className="sr-only">Close</span>
                    <svg className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12" />
                    </svg>
                  </button>
                </div>

                <div className="sm:flex sm:items-start">
                  <div className="mt-3 text-center sm:mt-0 sm:text-left w-full">
                    <h3 className="text-lg leading-6 font-medium text-gray-900">
                      Contact Us
                    </h3>
                    <div className="mt-4">
                      {submitStatus.show && (
                        <div className={`p-4 rounded-md mb-4 ${submitStatus.success ? 'bg-green-50' : 'bg-red-50'}`}>
                          <div className="flex">
                            <div className="flex-shrink-0">
                              {submitStatus.success ? (
                                <svg className="h-5 w-5 text-green-400" viewBox="0 0 20 20" fill="currentColor">
                                  <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                                </svg>
                              ) : (
                                <svg className="h-5 w-5 text-red-400" viewBox="0 0 20 20" fill="currentColor">
                                  <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                                </svg>
                              )}
                            </div>
                            <div className="ml-3">
                              <p className={`text-sm font-medium ${submitStatus.success ? 'text-green-800' : 'text-red-800'}`}>
                                {submitStatus.message}
                              </p>
                            </div>
                          </div>
                        </div>
                      )}
                      <form onSubmit={handleSubmit} className="space-y-6">
                        <div className="transform transition-all duration-300 ease-in-out hover:translate-x-1">
                          <label htmlFor="email" className="block text-sm font-medium text-gray-700">
                            Email address
                          </label>
                          <div className="mt-1">
                            <input
                              type="email"
                              name="email"
                              id="email"
                              value={email}
                              onChange={(e) => setEmail(e.target.value)}
                              className="shadow-sm focus:ring-green-500 focus:border-green-500 block w-full sm:text-sm border-gray-300 rounded-md transition-all duration-300 ease-in-out focus:shadow-md"
                              placeholder="you@example.com"
                              required
                            />
                          </div>
                        </div>

                        <div className="transform transition-all duration-300 ease-in-out hover:translate-x-1">
                          <label htmlFor="message" className="block text-sm font-medium text-gray-700">
                            What would you like to know?
                          </label>
                          <div className="mt-1">
                            <textarea
                              id="message"
                              name="message"
                              rows={4}
                              value={message}
                              onChange={(e) => setMessage(e.target.value)}
                              className="shadow-sm focus:ring-green-500 focus:border-green-500 block w-full sm:text-sm border-gray-300 rounded-md transition-all duration-300 ease-in-out focus:shadow-md"
                              placeholder="Your message here..."
                              required
                            />
                          </div>
                        </div>

                        <div className="mt-5 sm:mt-4 sm:flex sm:flex-row-reverse">
                          <button
                            type="submit"
                            disabled={isSubmitting}
                            className={`w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-green-600 text-base font-medium text-white hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 sm:ml-3 sm:w-auto sm:text-sm transform transition-all duration-300 ease-in-out hover:scale-105 hover:shadow-lg active:scale-95 ${isSubmitting ? 'opacity-75 cursor-not-allowed' : ''}`}
                          >
                            {isSubmitting ? 'Sending...' : 'Send Message'}
                          </button>
                          <button
                            type="button"
                            onClick={() => setIsModalOpen(false)}
                            className="mt-3 w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-white text-base font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 sm:mt-0 sm:w-auto sm:text-sm transform transition-all duration-300 ease-in-out hover:scale-105 hover:shadow-lg active:scale-95"
                          >
                            Cancel
                          </button>
                        </div>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default About;