import React, { useState, useRef, useEffect } from 'react';
import Web3 from 'web3';
import { QRCodeSVG } from 'qrcode.react';

function Wallet() {
  const [account, setAccount] = useState(() => localStorage.getItem('walletAccount') || '');
  const [balance, setBalance] = useState(() => localStorage.getItem('walletBalance') || '0');
  const [isConnected, setIsConnected] = useState(() => localStorage.getItem('walletConnected') === 'true');
  const [isSendFormOpen, setIsSendFormOpen] = useState(false);
  const [isReceiveModalOpen, setIsReceiveModalOpen] = useState(false);
  const [isBeneficiaryFormOpen, setIsBeneficiaryFormOpen] = useState(false);
  const [sendAddress, setSendAddress] = useState('');
  const [sendAmount, setSendAmount] = useState('');
  const [openForms, setOpenForms] = useState([]);
  const [copySuccess, setCopySuccess] = useState(false);
  const [beneficiaries, setBeneficiaries] = useState([]);
  const [newBeneficiary, setNewBeneficiary] = useState({
    name: '',
    address: '',
    relationship: '',
    percentage: '',
    inactivityPeriod: ''
  });
  const [sendStatus, setSendStatus] = useState({ show: false, success: false, message: '' });
  const [isLimitFormOpen, setIsLimitFormOpen] = useState(false);
  const [limitAmount, setLimitAmount] = useState('');
  const [currentLimit, setCurrentLimit] = useState(() => localStorage.getItem('walletLimit') || '0');
  const [receiveStatus, setReceiveStatus] = useState({ show: false, success: false, message: '' });
  const [beneficiaryStatus, setBeneficiaryStatus] = useState({ show: false, success: false, message: '' });
  const [limitStatus, setLimitStatus] = useState({ show: false, success: false, message: '' });

  const sendFormRef = useRef(null);
  const receiveFormRef = useRef(null);
  const beneficiaryFormRef = useRef(null);
  const limitFormRef = useRef(null);

  // Add effect to handle tab close
  useEffect(() => {
    const handleTabClose = () => {
      handleDisconnect();
    };

    window.addEventListener('beforeunload', handleTabClose);

    return () => {
      window.removeEventListener('beforeunload', handleTabClose);
    };
  }, []); // Empty dependency array since we only want to set up the listener once

  // Add effect to handle wallet events
  useEffect(() => {
    const checkConnection = async () => {
      if (window.ethereum && isConnected) {
        try {
          const web3 = new Web3(window.ethereum);
          const accounts = await web3.eth.getAccounts();
          
          if (accounts.length > 0) {
            setAccount(accounts[0]);
            localStorage.setItem('walletAccount', accounts[0]);
            
            const balance = await web3.eth.getBalance(accounts[0]);
            const balanceInEther = web3.utils.fromWei(balance, 'ether');
            setBalance(balanceInEther);
            localStorage.setItem('walletBalance', balanceInEther);
          } else {
            handleDisconnect();
          }
        } catch (error) {
          console.error('Error checking connection:', error);
          handleDisconnect();
        }
      }
    };

    const handleAccountsChanged = async (accounts) => {
      if (accounts.length === 0) {
        // User disconnected their wallet
        handleDisconnect();
      } else {
        // Account changed, update the state
        setAccount(accounts[0]);
        localStorage.setItem('walletAccount', accounts[0]);
        
        const web3 = new Web3(window.ethereum);
        const balance = await web3.eth.getBalance(accounts[0]);
        const balanceInEther = web3.utils.fromWei(balance, 'ether');
        setBalance(balanceInEther);
        localStorage.setItem('walletBalance', balanceInEther);
      }
    };

    const handleChainChanged = () => {
      // Reload the page when the chain changes
      window.location.reload();
    };

    // Check connection on component mount
    checkConnection();

    // Add event listeners
    if (window.ethereum) {
      window.ethereum.on('accountsChanged', handleAccountsChanged);
      window.ethereum.on('chainChanged', handleChainChanged);
      window.ethereum.on('disconnect', handleDisconnect);
    }

    // Cleanup event listeners
    return () => {
      if (window.ethereum) {
        window.ethereum.removeListener('accountsChanged', handleAccountsChanged);
        window.ethereum.removeListener('chainChanged', handleChainChanged);
        window.ethereum.removeListener('disconnect', handleDisconnect);
      }
    };
  }, [isConnected]);

  const handleDisconnect = () => {
    setAccount('');
    setBalance('0');
    setIsConnected(false);
    localStorage.removeItem('walletAccount');
    localStorage.removeItem('walletBalance');
    localStorage.removeItem('walletConnected');
  };

  const connectWallet = async () => {
    if (window.ethereum) {
      try {
        // Request account access
        await window.ethereum.request({ method: 'eth_requestAccounts' });
        const web3 = new Web3(window.ethereum);
        
        // Get account
        const accounts = await web3.eth.getAccounts();
        setAccount(accounts[0]);
        localStorage.setItem('walletAccount', accounts[0]);
        
        // Get balance
        const balance = await web3.eth.getBalance(accounts[0]);
        const balanceInEther = web3.utils.fromWei(balance, 'ether');
        setBalance(balanceInEther);
        localStorage.setItem('walletBalance', balanceInEther);
        
        setIsConnected(true);
        localStorage.setItem('walletConnected', 'true');
      } catch (error) {
        console.error('Error connecting wallet:', error);
      }
    } else {
      alert('Please install MetaMask!');
    }
  };

  const scrollToForm = (ref) => {
    ref.current?.scrollIntoView({ behavior: 'smooth', block: 'start' });
  };

  const handleSendFormToggle = () => {
    if (!isSendFormOpen) {
      setOpenForms(prev => [...prev, 'send']);
      setTimeout(() => scrollToForm(sendFormRef), 100);
    } else {
      setOpenForms(prev => prev.filter(form => form !== 'send'));
    }
    setIsSendFormOpen(!isSendFormOpen);
  };

  const handleReceiveFormToggle = () => {
    if (!isReceiveModalOpen) {
      setOpenForms(prev => [...prev, 'receive']);
      setTimeout(() => scrollToForm(receiveFormRef), 100);
    } else {
      setOpenForms(prev => prev.filter(form => form !== 'receive'));
    }
    setIsReceiveModalOpen(!isReceiveModalOpen);
  };

  const handleBeneficiaryFormToggle = () => {
    if (!isBeneficiaryFormOpen) {
      setOpenForms(prev => [...prev, 'beneficiary']);
      setTimeout(() => scrollToForm(beneficiaryFormRef), 100);
    } else {
      setOpenForms(prev => prev.filter(form => form !== 'beneficiary'));
    }
    setIsBeneficiaryFormOpen(!isBeneficiaryFormOpen);
  };

  const handleBeneficiaryInputChange = (e) => {
    const { name, value } = e.target;
    setNewBeneficiary(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleAddBeneficiary = async (e) => {
    e.preventDefault();
    if (!window.ethereum || !isConnected) {
      setBeneficiaryStatus({
        show: true,
        success: false,
        message: 'Please connect your wallet first'
      });
      return;
    }

    const web3 = new Web3(window.ethereum);
    if (!web3.utils.isAddress(newBeneficiary.address)) {
      setBeneficiaryStatus({
        show: true,
        success: false,
        message: 'Invalid wallet address'
      });
      return;
    }

    // Validate inactivity period
    const inactivityPeriodInDays = parseInt(newBeneficiary.inactivityPeriod);
    if (isNaN(inactivityPeriodInDays) || inactivityPeriodInDays < 1) {
      setBeneficiaryStatus({
        show: true,
        success: false,
        message: 'Please enter a valid inactivity period (minimum 1 day)'
      });
      return;
    }

    // Calculate total percentage including new beneficiary
    const totalPercentage = beneficiaries.reduce((sum, b) => sum + parseInt(b.percentage), 0) + parseInt(newBeneficiary.percentage);
    if (totalPercentage > 100) {
      setBeneficiaryStatus({
        show: true,
        success: false,
        message: 'Total percentage cannot exceed 100%'
      });
      return;
    }

    try {
      const beneficiaryWithId = {
        ...newBeneficiary,
        id: Date.now()
      };

      setBeneficiaries(prev => [...prev, beneficiaryWithId]);
      setNewBeneficiary({
        name: '',
        address: '',
        relationship: '',
        percentage: '',
        inactivityPeriod: ''
      });

      setBeneficiaryStatus({
        show: true,
        success: true,
        message: 'Beneficiary added successfully!'
      });
    } catch (error) {
      console.error('Error adding beneficiary:', error);
      setBeneficiaryStatus({
        show: true,
        success: false,
        message: 'Error adding beneficiary. Please try again.'
      });
    }
  };

  const handleRemoveBeneficiary = (id) => {
    setBeneficiaries(prev => prev.filter(b => b.id !== id));
  };

  const handleSendSubmit = async (e) => {
    e.preventDefault();
    if (!window.ethereum || !isConnected) return;

    try {
      const web3 = new Web3(window.ethereum);
      const amountInWei = web3.utils.toWei(sendAmount, 'ether');

      const transaction = {
        from: account,
        to: sendAddress,
        value: amountInWei,
      };

      await web3.eth.sendTransaction(transaction);
      
      // Reset form inputs but don't close the form
      setSendAddress('');
      setSendAmount('');
      
      // Update balance
      const newBalance = await web3.eth.getBalance(account);
      setBalance(web3.utils.fromWei(newBalance, 'ether'));

      // Show success message
      setSendStatus({
        show: true,
        success: true,
        message: 'Transaction completed successfully!'
      });
    } catch (error) {
      console.error('Error sending transaction:', error);
      setSendStatus({
        show: true,
        success: false,
        message: 'Error sending transaction. Please try again.'
      });
    }
  };

  const handleCloseSendForm = () => {
    setSendStatus({ show: false, success: false, message: '' });
    setIsSendFormOpen(false);
    setOpenForms(prev => prev.filter(form => form !== 'send'));
  };

  const handleCloseReceiveForm = () => {
    setReceiveStatus({ show: false, success: false, message: '' });
    setIsReceiveModalOpen(false);
    setOpenForms(prev => prev.filter(form => form !== 'receive'));
  };

  const handleCloseBeneficiaryForm = () => {
    setBeneficiaryStatus({ show: false, success: false, message: '' });
    setIsBeneficiaryFormOpen(false);
    setOpenForms(prev => prev.filter(form => form !== 'beneficiary'));
  };

  const copyToClipboard = async () => {
    try {
      await navigator.clipboard.writeText(account);
      setCopySuccess(true);
      setReceiveStatus({
        show: true,
        success: true,
        message: 'Address copied successfully!'
      });
      setTimeout(() => setCopySuccess(false), 2000);
    } catch (err) {
      console.error('Failed to copy address:', err);
      setReceiveStatus({
        show: true,
        success: false,
        message: 'Failed to copy address. Please try again.'
      });
    }
  };

  const handleLimitFormToggle = () => {
    if (!isLimitFormOpen) {
      setOpenForms(prev => [...prev, 'limit']);
      setTimeout(() => scrollToForm(limitFormRef), 100);
    } else {
      setOpenForms(prev => prev.filter(form => form !== 'limit'));
    }
    setIsLimitFormOpen(!isLimitFormOpen);
  };

  const handleSetLimit = async (e) => {
    e.preventDefault();
    if (!window.ethereum || !isConnected) {
      setLimitStatus({
        show: true,
        success: false,
        message: 'Please connect your wallet first'
      });
      return;
    }

    try {
      const web3 = new Web3(window.ethereum);
      const limitInEther = parseFloat(limitAmount);
      
      if (isNaN(limitInEther) || limitInEther <= 0) {
        setLimitStatus({
          show: true,
          success: false,
          message: 'Please enter a valid limit amount'
        });
        return;
      }

      // Store the limit in localStorage
      setCurrentLimit(limitAmount);
      localStorage.setItem('walletLimit', limitAmount);
      
      // Reset form
      setLimitAmount('');

      // Show success message
      setLimitStatus({
        show: true,
        success: true,
        message: 'Transaction limit set successfully!'
      });
    } catch (error) {
      console.error('Error setting limit:', error);
      setLimitStatus({
        show: true,
        success: false,
        message: 'Error setting limit. Please try again.'
      });
    }
  };

  const handleCloseLimitForm = () => {
    setLimitStatus({ show: false, success: false, message: '' });
    setIsLimitFormOpen(false);
    setOpenForms(prev => prev.filter(form => form !== 'limit'));
  };

  const renderForm = (formType) => {
    if (formType === 'send') {
      return (
        <div ref={sendFormRef} className="bg-gray-900/50 backdrop-blur-sm shadow rounded-lg p-6 transform transition-all duration-500 ease-in-out hover:shadow-lg animate-fadeIn">
          <div className="flex justify-between items-center mb-4">
            <h3 className="text-lg font-medium text-white">Send Transaction</h3>
            {!sendStatus.show && (
              <button
                onClick={handleSendFormToggle}
                className="text-gray-400 hover:text-gray-500 transition-colors duration-200"
              >
                <span className="sr-only">Close</span>
                <svg className="h-6 w-6 transform transition-transform duration-200 hover:rotate-90" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            )}
          </div>

          {sendStatus.show ? (
            <div className="space-y-4">
              <div className={`p-4 rounded-md ${sendStatus.success ? 'bg-green-50' : 'bg-red-50'}`}>
                <div className="flex">
                  <div className="flex-shrink-0">
                    {sendStatus.success ? (
                      <svg className="h-5 w-5 text-green-400" viewBox="0 0 20 20" fill="currentColor">
                        <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                      </svg>
                    ) : (
                      <svg className="h-5 w-5 text-red-400" viewBox="0 0 20 20" fill="currentColor">
                        <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                      </svg>
                    )}
                  </div>
                  <div className="ml-3">
                    <p className={`text-sm font-medium ${sendStatus.success ? 'text-green-800' : 'text-red-800'}`}>
                      {sendStatus.message}
                    </p>
                  </div>
                </div>
              </div>

              <div className="flex justify-center space-x-4">
                <button
                  onClick={() => setSendStatus({ show: false, success: false, message: '' })}
                  className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-green-600 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 transform transition-all duration-300 ease-in-out hover:scale-105 hover:shadow-lg active:scale-95"
                >
                  Send Another Transaction
                </button>
                <button
                  onClick={handleCloseSendForm}
                  className="inline-flex items-center px-4 py-2 border border-gray-300 text-sm font-medium rounded-md shadow-sm text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 transform transition-all duration-300 ease-in-out hover:scale-105 hover:shadow-lg active:scale-95"
                >
                  Close
                </button>
              </div>
            </div>
          ) : (
            <form onSubmit={handleSendSubmit} className="space-y-4">
              <div className="transform transition-all duration-300 ease-in-out hover:translate-x-1">
                <label htmlFor="address" className="block text-sm font-medium text-gray-300">
                  ADDRESS
                </label>
                <input
                  type="text"
                  id="address"
                  value={sendAddress}
                  onChange={(e) => setSendAddress(e.target.value)}
                  className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-green-500 focus:border-green-500 sm:text-sm transition-all duration-300 ease-in-out focus:shadow-md"
                  placeholder="0x..."
                  required
                />
              </div>
              <div className="transform transition-all duration-300 ease-in-out hover:translate-x-1">
                <label htmlFor="amount" className="block text-sm font-medium text-gray-300">
                  AMOUNT
                </label>
                <input
                  type="number"
                  id="amount"
                  value={sendAmount}
                  onChange={(e) => setSendAmount(e.target.value)}
                  className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-green-500 focus:border-green-500 sm:text-sm transition-all duration-300 ease-in-out focus:shadow-md"
                  placeholder="0.0"
                  step="0.000000000000000001"
                  min="0"
                  required
                />
              </div>
              <div className="flex justify-end">
                <button
                  type="submit"
                  className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-green-600 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 transform transition-all duration-300 ease-in-out hover:scale-105 hover:shadow-lg active:scale-95"
                >
                  SEND
                </button>
              </div>
            </form>
          )}
        </div>
      );
    } else if (formType === 'receive') {
      const metamaskURL = `ethereum:${account}`;
      
      return (
        <div ref={receiveFormRef} className="shadow rounded-lg p-6 transform transition-all duration-500 ease-in-out hover:shadow-lg animate-fadeIn">
          <div className="flex justify-between items-center mb-4">
            <h3 className="text-lg font-medium text-white">Receive Funds</h3>
            <button
              onClick={handleReceiveFormToggle}
              className="text-gray-400 hover:text-gray-500 transition-colors duration-200"
            >
              <span className="sr-only">Close</span>
              <svg className="h-6 w-6 transform transition-transform duration-200 hover:rotate-90" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          </div>

          {receiveStatus.show ? (
            <div className="space-y-6">
              <div className={`p-4 rounded-md ${receiveStatus.success ? 'bg-green-50' : 'bg-red-50'}`}>
                <div className="flex">
                  <div className="flex-shrink-0">
                    {receiveStatus.success ? (
                      <svg className="h-5 w-5 text-green-400" viewBox="0 0 20 20" fill="currentColor">
                        <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                      </svg>
                    ) : (
                      <svg className="h-5 w-5 text-red-400" viewBox="0 0 20 20" fill="currentColor">
                        <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                      </svg>
                    )}
                  </div>
                  <div className="ml-3">
                    <p className={`text-sm font-medium ${receiveStatus.success ? 'text-green-800' : 'text-red-800'}`}>
                      {receiveStatus.message}
                    </p>
                  </div>
                </div>
              </div>

              <div className="space-y-6">
                <p className="text-sm text-gray-500">
                  Scan this QR code or share your wallet address to receive cryptocurrency:
                </p>
                
                {/* QR Code Section */}
                <div className="flex flex-col items-center space-y-4">
                  <div className="bg-white p-4 rounded-lg shadow-sm border border-gray-200 transform transition-all duration-500 ease-in-out hover:shadow-lg hover:scale-105">
                    <QRCodeSVG
                      value={metamaskURL}
                      size={200}
                      level="H"
                      includeMargin={true}
                      imageSettings={{
                        src: "/images/elden-ring.webp",
                        x: undefined,
                        y: undefined,
                        height: 60,
                        width: 60,
                        excavate: true,
                      }}
                    />
                  </div>
                  <p className="text-xs text-gray-500">Scan with any crypto wallet app</p>
                </div>

                {/* Address Section */}
                <div className="space-y-2">
                  <p className="text-sm font-medium text-grey-700">Wallet Address:</p>
                  <div className="bg-gray-50 p-4 rounded-md border border-gray-200 transform transition-all duration-300 ease-in-out hover:shadow-md">
                    <p className="font-mono text-sm break-all">{account}</p>
                  </div>
                  <div className="flex justify-center mt-4">
                    <button
                      onClick={copyToClipboard}
                      className={`inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white 
                        ${copySuccess ? 'bg-green-500' : 'bg-green-600 hover:bg-green-700'} 
                        focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 
                        transform transition-all duration-300 ease-in-out hover:scale-105 hover:shadow-lg active:scale-95`}
                    >
                      {copySuccess ? 'Copied!' : 'Copy Address'}
                    </button>
                  </div>
                </div>
              </div>
            </div>
          ) : (
            <div className="space-y-6">
              <p className="text-sm text-gray-500">
                Scan this QR code or share your wallet address to receive cryptocurrency:
              </p>
              
              {/* QR Code Section */}
              <div className="flex flex-col items-center space-y-4">
                <div className="bg-white p-4 rounded-lg shadow-sm border border-gray-200 transform transition-all duration-500 ease-in-out hover:shadow-lg hover:scale-105">
                  <QRCodeSVG
                    value={metamaskURL}
                    size={200}
                    level="H"
                    includeMargin={true}
                    imageSettings={{
                      src: "/images/elden-ring.webp",
                      x: undefined,
                      y: undefined,
                      height: 60,
                      width: 60,
                      excavate: true,
                    }}
                  />
                </div>
                <p className="text-xs text-gray-500">Scan with any crypto wallet app</p>
              </div>

              {/* Address Section */}
              <div className="space-y-2">
                <p className="text-sm font-medium text-white">Wallet Address:</p>
                <div className="bg-gray-50 p-4 rounded-md border border-gray-200 transform transition-all duration-300 ease-in-out hover:shadow-md">
                  <p className="font-mono text-sm break-all text-black">{account}</p>
                </div>
                <div className="flex justify-center mt-4">
                  <button
                    onClick={copyToClipboard}
                    className={`inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white 
                      ${copySuccess ? 'bg-green-500' : 'bg-green-600 hover:bg-green-700'} 
                      focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 
                      transform transition-all duration-300 ease-in-out hover:scale-105 hover:shadow-lg active:scale-95`}
                  >
                    {copySuccess ? 'Copied!' : 'Copy Address'}
                  </button>
                </div>
              </div>
            </div>
          )}
        </div>
      );
    } else if (formType === 'beneficiary') {
      return (
        <div ref={beneficiaryFormRef} className="shadow rounded-lg p-6 transform transition-all duration-500 ease-in-out hover:shadow-lg animate-fadeIn">
          <div className="flex justify-between items-center mb-4">
            <h3 className="text-lg font-medium text-white">Add Beneficiary</h3>
            {!beneficiaryStatus.show && (
              <button
                onClick={handleBeneficiaryFormToggle}
                className="text-gray-400 hover:text-gray-500 transition-colors duration-200"
              >
                <span className="sr-only">Close</span>
                <svg className="h-6 w-6 transform transition-transform duration-200 hover:rotate-90" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            )}
          </div>

          {beneficiaryStatus.show ? (
            <div className="space-y-4">
              <div className={`p-4 rounded-md ${beneficiaryStatus.success ? 'bg-green-50' : 'bg-red-50'}`}>
                <div className="flex">
                  <div className="flex-shrink-0">
                    {beneficiaryStatus.success ? (
                      <svg className="h-5 w-5 text-green-400" viewBox="0 0 20 20" fill="currentColor">
                        <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                      </svg>
                    ) : (
                      <svg className="h-5 w-5 text-red-400" viewBox="0 0 20 20" fill="currentColor">
                        <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                      </svg>
                    )}
                  </div>
                  <div className="ml-3">
                    <p className={`text-sm font-medium ${beneficiaryStatus.success ? 'text-green-800' : 'text-red-800'}`}>
                      {beneficiaryStatus.message}
                    </p>
                  </div>
                </div>
              </div>

              <div className="flex justify-center space-x-4">
                <button
                  onClick={() => setBeneficiaryStatus({ show: false, success: false, message: '' })}
                  className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-green-600 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 transform transition-all duration-300 ease-in-out hover:scale-105 hover:shadow-lg active:scale-95"
                >
                  Add Another Beneficiary
                </button>
                <button
                  onClick={handleCloseBeneficiaryForm}
                  className="inline-flex items-center px-4 py-2 border border-gray-300 text-sm font-medium rounded-md shadow-sm text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 transform transition-all duration-300 ease-in-out hover:scale-105 hover:shadow-lg active:scale-95"
                >
                  Close
                </button>
              </div>
            </div>
          ) : (
            <form onSubmit={handleAddBeneficiary} className="space-y-4">
              <div className="transform transition-all duration-300 ease-in-out hover:translate-x-1">
                <label htmlFor="name" className="block text-sm font-medium text-gray-300">
                  Name
                </label>
                <input
                  type="text"
                  id="name"
                  name="name"
                  value={newBeneficiary.name}
                  onChange={handleBeneficiaryInputChange}
                  className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-green-500 focus:border-green-500 sm:text-sm"
                  required
                />
              </div>
              <div className="transform transition-all duration-300 ease-in-out hover:translate-x-1">
                <label htmlFor="address" className="block text-sm font-medium text-gray-300">
                  Wallet Address
                </label>
                <input
                  type="text"
                  id="address"
                  name="address"
                  value={newBeneficiary.address}
                  onChange={handleBeneficiaryInputChange}
                  className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-green-500 focus:border-green-500 sm:text-sm"
                  placeholder="0x..."
                  required
                />
              </div>
              <div className="transform transition-all duration-300 ease-in-out hover:translate-x-1">
                <label htmlFor="relationship" className="block text-sm font-medium text-gray-300">
                  Relationship
                </label>
                <input
                  type="text"
                  id="relationship"
                  name="relationship"
                  value={newBeneficiary.relationship}
                  onChange={handleBeneficiaryInputChange}
                  className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-green-500 focus:border-green-500 sm:text-sm"
                  required
                />
              </div>
              <div className="transform transition-all duration-300 ease-in-out hover:translate-x-1">
                <label htmlFor="percentage" className="block text-sm font-medium text-gray-300">
                  Percentage Share
                </label>
                <input
                  type="number"
                  id="percentage"
                  name="percentage"
                  value={newBeneficiary.percentage}
                  onChange={handleBeneficiaryInputChange}
                  className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-green-500 focus:border-green-500 sm:text-sm"
                  min="0"
                  max="100"
                  required
                />
              </div>
              <div className="transform transition-all duration-300 ease-in-out hover:translate-x-1">
                <label htmlFor="inactivityPeriod" className="block text-sm font-medium text-gray-300">
                  Inactivity Period (days)
                </label>
                <div className="mt-1 relative rounded-md shadow-sm">
                  <input
                    type="number"
                    id="inactivityPeriod"
                    name="inactivityPeriod"
                    value={newBeneficiary.inactivityPeriod}
                    onChange={handleBeneficiaryInputChange}
                    className="block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-green-500 focus:border-green-500 sm:text-sm"
                    min="1"
                    placeholder="e.g., 180 days"
                    required
                  />
                  <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
                    <span className="text-gray-500 sm:text-sm">days</span>
                  </div>
                </div>
                <p className="mt-1 text-sm text-gray-300">
                  Minimum period of inactivity before beneficiary can claim funds
                </p>
              </div>
              <div className="flex justify-end">
                <button
                  type="submit"
                  className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-green-600 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 transform transition-all duration-300 ease-in-out hover:scale-105 hover:shadow-lg active:scale-95"
                >
                  Add Beneficiary
                </button>
              </div>
            </form>
          )}
        </div>
      );
    } else if (formType === 'limit') {
      return (
        <div ref={limitFormRef} className="shadow rounded-lg p-6 transform transition-all duration-500 ease-in-out hover:shadow-lg animate-fadeIn">
          <div className="flex justify-between items-center mb-4">
            <h3 className="text-lg font-medium text-white">Set Transaction Limit</h3>
            {!limitStatus.show && (
              <button
                onClick={handleLimitFormToggle}
                className="text-gray-400 hover:text-gray-500 transition-colors duration-200"
              >
                <span className="sr-only">Close</span>
                <svg className="h-6 w-6 transform transition-transform duration-200 hover:rotate-90" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            )}
          </div>
          
          {limitStatus.show ? (
            <div className="space-y-4">
              <div className={`p-4 rounded-md ${limitStatus.success ? 'bg-green-50' : 'bg-red-50'}`}>
                <div className="flex">
                  <div className="flex-shrink-0">
                    {limitStatus.success ? (
                      <svg className="h-5 w-5 text-green-400" viewBox="0 0 20 20" fill="currentColor">
                        <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                      </svg>
                    ) : (
                      <svg className="h-5 w-5 text-red-400" viewBox="0 0 20 20" fill="currentColor">
                        <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                      </svg>
                    )}
                  </div>
                  <div className="ml-3">
                    <p className={`text-sm font-medium ${limitStatus.success ? 'text-green-800' : 'text-red-800'}`}>
                      {limitStatus.message}
                    </p>
                  </div>
                </div>
              </div>

              <div className="flex justify-center space-x-4">
                <button
                  onClick={() => setLimitStatus({ show: false, success: false, message: '' })}
                  className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-green-600 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 transform transition-all duration-300 ease-in-out hover:scale-105 hover:shadow-lg active:scale-95"
                >
                  Set Another Limit
                </button>
                <button
                  onClick={handleCloseLimitForm}
                  className="inline-flex items-center px-4 py-2 border border-gray-300 text-sm font-medium rounded-md shadow-sm text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 transform transition-all duration-300 ease-in-out hover:scale-105 hover:shadow-lg active:scale-95"
                >
                  Close
                </button>
              </div>
            </div>
          ) : (
            <form onSubmit={handleSetLimit} className="space-y-4">
              <div className="transform transition-all duration-300 ease-in-out hover:translate-x-1">
                <label htmlFor="limit" className="block text-sm font-medium text-gray-300">
                  Daily Transaction Limit (ETH)
                </label>
                <div className="mt-1 relative rounded-md shadow-sm">
                  <input
                    type="number"
                    id="limit"
                    value={limitAmount}
                    onChange={(e) => setLimitAmount(e.target.value)}
                    className="block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-green-500 focus:border-green-500 sm:text-sm"
                    placeholder="Enter limit amount"
                    step="0.000000000000000001"
                    min="0"
                    required
                  />
                  <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
                    <span className="text-gray-500 sm:text-sm">ETH</span>
                  </div>
                </div>
                {currentLimit !== '0' && (
                  <p className="mt-2 text-sm text-gray-300">
                    Current limit: {currentLimit} ETH
                  </p>
                )}
              </div>
              <div className="flex justify-end">
                <button
                  type="submit"
                  className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-green-600 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 transform transition-all duration-300 ease-in-out hover:scale-105 hover:shadow-lg active:scale-95"
                >
                  Set Limit
                </button>
              </div>
            </form>
          )}
        </div>
      );
    }
    return null;
  };

  return (
    <div className="relative min-h-screen bg-gradient-to-b from-black to-gray-800 text-white overflow-hidden">
      {/* Background video */}
      <video
        autoPlay
        loop
        muted
        className="absolute inset-0 w-full h-full object-cover opacity-30"
        style={{ filter: 'brightness(0.4)' }}
      >
        <source src="/videos/background.mp4" type="video/mp4" />
      </video>

      {/* Content */}
      <div className="relative z-10 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 space-y-6 py-12">
        <div className="py-4">
          <h2 className="text-3xl font-bold text-white">Your Wallet</h2>
          <p className="mt-1 text-sm text-gray-300">
            Connect your wallet to view your balance and make transactions
          </p>
        </div>

        <div className="bg-gray-900/50 backdrop-blur-sm shadow rounded-lg p-6 transform transition-all duration-500 ease-in-out hover:shadow-lg opacity-0 animate-slideInFromBottom">
          {!isConnected ? (
            <div className="text-center">
              <button
                onClick={connectWallet}
                className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-green-600 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 transform transition-all duration-300 ease-in-out hover:scale-105 hover:shadow-lg active:scale-95"
              >
                Connect Wallet
              </button>
            </div>
          ) : (
            <div className="space-y-6">
              <div className="bg-gray-800/50 backdrop-blur-sm px-4 py-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6 rounded-lg transform transition-all duration-300 ease-in-out hover:shadow-md">
                <dt className="text-sm font-medium text-gray-300">Account Address</dt>
                <dd className="mt-1 text-sm text-gray-100 sm:col-span-2 sm:mt-0 font-mono">
                  {account}
                </dd>
              </div>
              
              <div className="bg-gray-800/50 backdrop-blur-sm px-4 py-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6 rounded-lg transform transition-all duration-300 ease-in-out hover:shadow-md">
                <dt className="text-sm font-medium text-gray-300">Balance</dt>
                <dd className="mt-1 text-sm text-gray-100 sm:col-span-2 sm:mt-0">
                  {parseFloat(balance).toFixed(4)} ETH
                </dd>
              </div>

              <div className="flex space-x-4 justify-center">
                <button
                  onClick={handleSendFormToggle}
                  className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-green-600 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 transform transition-all duration-300 ease-in-out hover:scale-105 hover:shadow-lg active:scale-95"
                >
                  Send
                </button>
                <button
                  onClick={handleReceiveFormToggle}
                  className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-green-600 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 transform transition-all duration-300 ease-in-out hover:scale-105 hover:shadow-lg active:scale-95"
                >
                  Receive
                </button>
                <button
                  onClick={handleBeneficiaryFormToggle}
                  className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-green-600 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 transform transition-all duration-300 ease-in-out hover:scale-105 hover:shadow-lg active:scale-95"
                >
                  Add Beneficiary
                </button>
                <button
                  onClick={handleLimitFormToggle}
                  className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-green-600 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 transform transition-all duration-300 ease-in-out hover:scale-105 hover:shadow-lg active:scale-95"
                >
                  Set Limit
                </button>
              </div>
            </div>
          )}
        </div>

        {/* Forms */}
        <div className="space-y-6">
          {openForms.map((formType) => (
            <div key={formType} className="transform transition-all duration-500 ease-in-out opacity-0 animate-fadeIn">
              {renderForm(formType)}
            </div>
          ))}
        </div>

        {/* Beneficiaries List */}
        {beneficiaries.length > 0 && (
          <div className="bg-gray-900/50 backdrop-blur-sm shadow rounded-lg p-6 transform transition-all duration-500 ease-in-out hover:shadow-lg animate-fadeIn">
            <h3 className="text-lg font-medium text-white mb-4">Your Beneficiaries</h3>
            <div className="space-y-4">
              {beneficiaries.map((beneficiary) => (
                <div
                  key={beneficiary.id}
                  className="bg-gray-800/50 backdrop-blur-sm p-4 rounded-lg flex justify-between items-center transform transition-all duration-300 ease-in-out hover:shadow-md"
                >
                  <div>
                    <h4 className="text-sm font-medium text-white">{beneficiary.name}</h4>
                    <p className="text-sm text-gray-300">
                      {beneficiary.address.slice(0, 6)}...{beneficiary.address.slice(-4)}
                    </p>
                    <p className="text-sm text-gray-300">{beneficiary.relationship}</p>
                    <p className="text-sm text-gray-300">{beneficiary.percentage}%</p>
                    <p className="text-sm text-gray-300">Inactivity Period: {beneficiary.inactivityPeriod} days</p>
                  </div>
                  <button
                    onClick={() => handleRemoveBeneficiary(beneficiary.id)}
                    className="text-red-400 hover:text-red-300 focus:outline-none"
                  >
                    Remove
                  </button>
                </div>
              ))}
              <div className="mt-4 p-4 bg-gray-800/50 backdrop-blur-sm rounded-lg">
                <p className="text-sm text-gray-300">
                  Total Allocated: {beneficiaries.reduce((sum, b) => sum + parseInt(b.percentage), 0)}%
                </p>
                <p className="text-sm text-gray-300">
                  Remaining: {100 - beneficiaries.reduce((sum, b) => sum + parseInt(b.percentage), 0)}%
                </p>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default Wallet;